<template>
  <div>
      <div class="row">
      <div class="col-lg-12">
        <h4 class="font-weight-bold py-3 mb-1">
          <span class="text-muted font-weight-light">Reklamlar</span>
        </h4>
      </div>
      <b-col class="offset-10">
          <b-btn variant="primary rounded-pill" class="align-self-start" to="/ads/create"><span class="fas fa-plus-circle"></span> Yeni Reklam Ekle</b-btn>
      </b-col>
    </div>


    <b-table
      class="table nexus-table mt-2" hover  
                      borderless 
        :items="adsTableData"
        :fields="tableFields">
      <template #cell(adImage)="data" >
          <img class="post-table-image" :src="data.value" with="75" height="75" @click="previewImage(data.value)" />
      </template>
      <template #cell(details)="data" class="activities-table-buttons">
        <span title="Edit Post" class="far fa-edit mr-2 text-primary" @click="goToAd(data)"></span>
        <span title="Delete Post" class="fas fa-times text-danger" @click="deleteAd(data)"></span>
      </template>
    </b-table>

  <div v-show="previewToggle" class="preview-container" @click="previewToggle = false;">
    <span class="close">&times;</span>

    <div id="preview">
      <img  :src="previewImageUrl" @click="previewToggle = false"/>
    </div>

    <div class="caption">
      {{previewImageName}} 
    </div>
  </div>

</div>
</template>
<script>
import axios from "axios";
import moment from 'moment';
moment.locale('tr')

export default {
  name: "ads",
  metaInfo: {
    title: "Ads",
  },
  data: () => ({
    tableFields: [
      {
        key: "adImage",
        label: "Reklam Resmi",
        sortable: true,
        sortDirection: "desc",
        class: "text-center align-middle",
      },
      {
        key: "Baslik",
        label: 'Reklam Adi',
        sortable: true,
        sortDirection: "desc",
        class: "text-center align-middle",
      },
      {
        key: "owner",
        label: "Ekleyen",
        sortable: true,
        sortDirection: "desc",
        class: "text-center align-middle",
      },
      {
        key: "adType",
        label: "Reklam Türü",
        sortable: true,
        sortDirection: "desc",
        class: "text-center align-middle",
      },
      {
        key: "position",
        label: "Reklam Konumu",
        sortable: true,
        sortDirection: "desc",
        class: "text-center align-middle",
      },
      {
        key: "status",
        label: "Reklam Durumu",
        sortable: true,
        sortDirection: "desc",
        class: "text-center align-middle",
      },
      {
        key: "details",
        label: 'İşlemler',
        sortable: "true",
        class: "text-center align-middle",
      }
    ],
    ads: [],
    adsTableData: [],
    originalAdsTableData: [],
    previewImageUrl: null,
    previewImageName: '',
    previewToggle: false,
  }),
  created() {
    var vm = this
      
    axios
      .post(process.env.VUE_APP_SERVER_URL + "/get_ads/", {})
      .then((response) => {
        console.log(response.data);
        vm.ads= response.data
            vm.ads.forEach(ad=> {
              var tmp_ad= {
                owner: ad.owner,
                position: ad.adPosition || '---',
                status: ad.status,
                adType: ad.adType,
                adImage: process.env.VUE_APP_SERVER_URL + '/images/' + ad.adImage,
                Baslik: ad.adTitle,
                details: ad._id
              }
              vm.adsTableData.push(tmp_ad)
            })
            vm.originalAdsTableData = vm.adsTableData
      });

  },
  computed: {
  },
  methods: {
    goToAd(data) {
      this.$router.push({ name: 'Ad', params: { id: data.value } })
    },
    deleteAd(data) {
      console.log(data)
    },
    previewImage(imgName) {
      var vm = this
      vm.previewToggle = !vm.previewToggle

      if (vm.previewImageUrl) vm.previewImageUrl = null
      vm.previewImageUrl = imgName
      vm.previewImageName = imgName.split('/').pop()
    },
  },
};
</script>

<style>

.preview-container {
  position: fixed;
  z-index: 10;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0,0,0,0.8);
  width: 100vw;
  height: 100vh;
}

#preview {
  position:relative;
  top: 50%;
  left: 50%;
  height: 100%;
  animation-name: zoom;
  animation-duration: 0.8s;
}

@keyframes zoom {
  from {transform:scale(0)}
  to {transform:scale(1)}
}

#preview img {
  transform: translate(-50%, -50%);
}

.post-table-image {
  width: 75px;
  height: 75px;
}

.caption {
  position: relative;
  top: -10%;
  left: 25%;
  margin: auto;
  display: block;
  width: 80%;
  max-width: 700px;
  text-align: center;
  color: #ccc;
  padding: 10px 0;
  animation-name: zoom;
  animation-duration: 0.8s;
}

.activities-table-buttons {
  display: flex;
  align-items: center;
  justify-content: center;
}
</style>